// /components/AntiCheating.jsx
import { useEffect, useRef, useState } from "react";

function loadScript(src, id) {
  return new Promise((resolve, reject) => {
    if (document.getElementById(id)) {
      resolve();
    } else {
      const script = document.createElement("script");
      script.src = src;
      script.id = id;
      script.async = true;
      script.onload = () => resolve();
      script.onerror = () =>
        reject(new Error(`Failed to load script: ${src}`));
      document.body.appendChild(script);
    }
  });
}

function AntiCheating({ onCheatingDetected }) {
  const videoRef = useRef(null);
  const [isCheating, setIsCheating] = useState(false);

  useEffect(() => {
    if (!videoRef.current) return;

    // Async initialization function.
    const initialize = async () => {
      try {
        // Load Face Detection script from CDN.
        await loadScript(
          "https://cdn.jsdelivr.net/npm/@mediapipe/face_detection/face_detection.js",
          "mediapipe-face-detection"
        );
        // Load Camera Utils script from CDN.
        await loadScript(
          "https://cdn.jsdelivr.net/npm/@mediapipe/camera_utils/camera_utils.js",
          "mediapipe-camera-utils"
        );

        // Now access FaceDetection from the global window.
        const FaceDetection = window.FaceDetection;
        if (!FaceDetection) {
          console.error("FaceDetection is not available on window.");
          return;
        }

        // Create an instance of FaceDetection.
        const faceDetection = new FaceDetection({
          locateFile: (file) =>
            `https://cdn.jsdelivr.net/npm/@mediapipe/face_detection/${file}`,
        });

        faceDetection.setOptions({
          model: "short",
          minDetectionConfidence: 0.5,
        });

        faceDetection.onResults((results) => {
          // Flag cheating if no faces are detected.
          if (!results.detections || results.detections.length === 0) {
            setIsCheating(true);
            onCheatingDetected();
          } else {
            setIsCheating(false);
          }
        });

        let cameraInstance;
        if (videoRef.current && window.Camera) {
          cameraInstance = new window.Camera(videoRef.current, {
            onFrame: async () => {
              await faceDetection.send({ image: videoRef.current });
            },
            width: 640,
            height: 480,
          });
          cameraInstance.start();
        }

        // Cleanup function: stop the camera when component unmounts.
        return () => {
          if (cameraInstance && cameraInstance.stop) {
            cameraInstance.stop();
          }
        };
      } catch (error) {
        console.error(error);
      }
    };

    initialize();
  }, [onCheatingDetected]);

  return (
    <div>
      {/* Hidden video element used for processing */}
      <video ref={videoRef} className="hidden" playsInline></video>
      {isCheating && (
        <div className="fixed top-4 left-4 bg-red-600 text-white px-4 py-2 rounded shadow-md">
          Warning: Cheating Detected!
        </div>
      )}
    </div>
  );
}

export default AntiCheating;
